body {
    font: 14px "Century Gothic", Futura, sans-serif;
    margin: 20px;
  }
  
  ol, ul {
    padding-left: 30px;
  }
  
  .board-row:after {
    clear: both;
    content: "";
    display: table;
  }
  
  .status {
    margin-bottom: 10px;
  }
  
  .square {
    background: #fff;
    border: 1px solid #999;
    float: left;
    font-size: 24px;
    font-weight: bold;
    line-height: 34px;
    /* height: 34px;
    width: 34px; */

    margin-right: -1px;
    margin-top: -1px;
    padding: 0;
    text-align: center;
  }
  
  .square:focus {
    outline: none;
  }
  
  .kbd-navigation .square:focus {
    background: #ddd;
  }
  
  .game {
    display: flex;
    flex-direction: column;
  }

  .tate {
    display: flex;
    flex-direction: column;
  }
  .yoko{
    display: flex;
    flex-direction: row;
  }
  .cb{
    margin-right: 25px;
  }
  
  .game-info {
    margin-left: 20px;
  }

  .td7{
    background-color: rgb(19, 30, 180);
  }
  .td6{
    background-color: rgb(173, 2, 2);
  }
  .td5{
    background-color: rgb(212, 116, 5);
  }
  .td4{
    background-color: rgb(32, 126, 3);
  }
  .td3{
    background-color: rgb(41, 177, 201);
  }
  .td2{
    background-color: rgb(255, 255, 255);
  }
  .td1{
    background-color: rgb(98, 34, 110);
  }
  .td0{
    background-color: rgb(77, 77, 77);
  }
  .tdmyhand::after{
    content:"@";
  }
  .atari{
    padding: 0.1rem 0.1rem;
    border: 3px solid;
    font-size: large;
  }
